import React from "react";
import styles from "./order-details.module.css";
import submittedOrderImg from "../../../images/submitted-order.svg";
import {useSelector} from 'react-redux';
import {
    selectOrderId,
    selectHasErrorOrderId,
    selectIsLoadingOrderId
} from "../../../services/orderDetails/selector";
import useLoadingAndErrorHandling from "../../../hooks/useLoadingAndErrorHandling";
import LoadingComponent from "../../../utils/loading-component";
import ErrorComponent from "../../../utils/error-component";

const OrderDetails = () => {
    const {isLoading, hasError} = useLoadingAndErrorHandling(selectIsLoadingOrderId, selectHasErrorOrderId);
    const orderId = useSelector(selectOrderId);

    return (
        <>
            {isLoading &&
                <div className={styles.backdrop}>
                    <LoadingComponent/>
                </div>}
            {!isLoading && hasError &&
                <div className={styles.error}>
                    <ErrorComponent/>
                </div>}
            {!isLoading && !hasError &&
                <div className={styles.container}>
                    <h3 className={`${styles['order-id']} text text_type_digits-large mb-8`}>{orderId}</h3>
                    <p className="text text_type_main-medium"> </p>
                    <img className={styles.img} src={submittedOrderImg} alt=' .' width='110' height='110'/>
                    <p className="text text_type_main-default mb-2">   </p>
                    <p className="text text_type_main-default text_color_inactive">   
                        </p>
                </div>
            }
        </>
    );
};

export default OrderDetails;